a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
tbody,
tfoot,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
    margin: 0;
    padding: 0;
    border: 0;
    vertical-align: baseline
}

body,
fieldset,
img {
    padding: 0;
    vertical-align: baseline
}

fieldset,
img {
    margin: 0
}

td,
th {
    border-bottom: 1px solid #efefef;
    margin: 0;
    padding-right: 5px;
}

html {
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box
}

a img,
body,
fieldset,
img {
    border: 0
}

input[type=email],
input[type=tel],
input[type=text],
textarea {
    -webkit-appearance: none
}

a,
a:focus,
button,
div,
h1,
h2,
h3,
h4,
h5,
h6,
input,
select,
span,
textarea {
    outline: 0
}

ol,
ul {
    list-style-type: none
}

table {
    border-spacing: 0;
    border-collapse: collapse;
    margin: 0 auto 50px;
}

*,
:after,
:before {
    -webkit-box-sizing: inherit;
    box-sizing: inherit
}

@media (prefers-reduced-motion:reduce) {
    * {
        -webkit-transition: none!important;
        -o-transition: none!important;
        transition: none!important
    }
}

body {
    background: #fff;
    margin: 0;
    font-family: Arial, sans-serif;
    background: #f7f8f9
}

header {
    position: fixed;
    top: 0;
    width: 100%;
    background: #ffde28;
    min-height: 24px;
    padding: 3px 10px;
    text-align: center;
    display: flex;
    justify-content: space-between;
}

.control-block {
    display: flex;
    align-items: center;
}

.toggle-btn {
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    display: inline-block;
    box-sizing: border-box;
    font-size: 23px;
    width: 24px;
    height: 22px;
    padding: 0px;
}

.toggle-btn__icon {
    display: inline-block;
    vertical-align: middle;
    box-sizing: border-box;
    margin-top: -0.47em;
    margin-left: -0.05em;
}

.search-input {
    width: 140px;
}

select {
    margin: 0 1px;
    height: 22px;
}

.main-title {
    margin: 50px 0;
    text-align: center;
}

.container {
    max-width: 940px;
    margin: 0 auto;
    padding: 1%;
}

.goods-grid {
    display: grid;
    grid-template-columns: repeat(4, 2fr);
    grid-gap: 14px;
}

.goods-card {
    box-sizing: border-box;
    text-align: center;
    border: 1px solid gray;
    width: 100%;
    min-height: 200px;
    padding: 1%;
}

.goods-card__title {
    font-size: 14px;
}

.goods-table__title {
    text-align: left;
    font-size: 14px;
    line-height: 2.143;
    background: #d6dfe6;
}

.goods-table__img {
    width: 30px;
    height: 30px;
    object-fit: fill;
    margin: 0 5px 0 5px;
}

td:nth-child(1) {
    vertical-align: middle
}

.img-template {
    min-height: 180px;
    width: 100%;
}

.img-template__img {
    width: 100%;
    height: auto;
    object-fit: fill;
}

@media only screen and (max-width:700px) {
    .goods-grid {
        padding: 0 1%;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 4px;
    }
}

@media only screen and (max-width:500px) {
    .goods-grid {
        padding: 0 1% 1% 1%;
        grid-template-columns: repeat(2, 1fr);
    }
    select {
        font-size: 10px;
    }
}